@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
:root {
  /* Color Palette Selection */
  --navy-lightest: hsl(235, 21%, 32%);
  --navy-lighter: hsl(235, 21%, 25%);
  --navy: hsl(235, 21%, 21%);
  --navy-darker: hsl(235, 21%, 17%);
  --navy-darker: hsl(235, 21%, 12%);

  --teal-lighter: hsl(165, 74%, 63%);
  --teal: #41E2BA;
  --teal-darker: hsl(165, 74%, 52%);
  --teal-darker: hsl(165, 74%, 42%);

  --white: hsl(240, 14%, 97%);
  --white-darker: hsl(240, 14%, 93%);
  --gray: hsl(0, 0%, 50%);
  --gray-darker: hsl(0, 0%, 35%);
  --gray-darkest: hsl(0, 0%, 20%);
  --yellow: #F7E733;
  --orange: #EE6C4D;
  --max-content-width: 900px;
}

.background-white {
  background-color: hsl(240, 14%, 97%);
  background-color: var(--white);
}

.background-teal {
  background-color: #41E2BA;
  background-color: var(--teal);
}

.background-navy {
  background-color: hsl(235, 21%, 21%);
  background-color: var(--navy);
}

.color-white {
  color: hsl(240, 14%, 97%);
  color: var(--white);
}

.color-teal {
  color: #41E2BA;
  color: var(--teal);
}

.color-navy {
  color: hsl(235, 21%, 21%);
  color: var(--navy);
}

html {
  scroll-behavior: smooth;
  font-family: "Raleway";
  /* color: var(--white); */
  background-color: hsl(240, 14%, 97%);
  background-color: var(--white);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.flex.row {
  flex-direction: row;
}
.navbar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  background: var(--navy);
  font-size: 20px;
  color: var(--white);
  padding: 0.5em 0;
  position: -webkit-sticky;
  position: sticky;
  z-index: 100;
  top: 0;
  border-bottom: 2px solid var(--teal);
}

.navbar-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  max-width: var(--max-content-width);
}

.navbaar-links {
  display: flex;
  flex-direction: row;
}

.navbar-link {
  display: inline;
  margin: 0 20px;
}

footer {
  background: var(--navy);
  color: var(--white-darker);
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 70px 0 50px 0;
  grid-gap: 20px;
  gap: 20px;
}

.footnote {
  color: rgba(247,247,249, 0.35);
  text-align: center;
}

.icons{
  display: flex;
  flex-direction: row;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
}

.social-icon {
  background: var(--navy-lighter);
  padding: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.2s 0s;
}


.social-icon:hover {
  background: var(--orange);
}

.footer-icon {
  fill: var(--white);
  font-size: 2em;
}
.highlight {
  color: var(--teal);
}

.banner {
  display:flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: calc(100vh);;
  margin:0;
  padding:0;
  background-color: var(--navy);
  color: var(--white);
  font-weight: normal;
  font-size: 50px;
  font-family: "Raleway";
}

.banner-bg-canvas {
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.banner-text {
  z-index: 1;
  pointer-events: none;
}
.home-content {
  width: 100%;
  min-height: 100vh;
  margin: 0px auto;
}

.home-content section {
  margin: 0px auto;
}

.home-content section:nth-child(even) {
  background-color: var(--white-darker);
}

.about-me-section {
  display: flex;
  justify-content: center;
}

.about-me-content {
  display: grid;
  grid-template-columns: 4fr 2fr;
  padding-bottom: 40px;
  grid-gap: 50px;
  gap: 50px;
}

.about-me-description {
  text-align: justify;
}

.about-me-content {
  max-width: var(--max-content-width);
}


.about-me-profile-container {
  height: 250px;
  width: 250px;
  position: relative;
  box-sizing: border-box;
  z-index: 0;
}

.about-me-profile-inner {
  width: 100%;
  height: 100%;
  position: absolute;
  border: 4px solid var(--teal);
  border-radius: 5px;
  top: 20px;
  left: 20px;
  z-index: -1;
  box-sizing: border-box;
}

.profile-pic {
  z-index: 0;
  border-radius: 5px;
  width: 100%;
}
.header-underline {
    width: 100px;
    height: 3px;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 100px;
    padding: 0;
    background: var(--navy);
}
.experience-section {
    display: flex;
    justify-content: center;
    padding: 20px 0px;
}

.experience-content {
    width: 100%;
    max-width: var(--max-content-width);
    min-height: 400px;
}

.experience-viewer {
    display:  grid;
    grid-template-columns: 2fr 5fr;
    width: var(--max-content-width);
}

.tabpanel {
    display: flex;
    flex-direction: column;
    padding: 0 20px;
}

/* experience panel descriptions */

.experience-title {
    margin: 0px;
}

.experience-range {
    color: rgb(117, 117, 117);
    margin: 0;
}

.experience-description {
    color: rgb(107, 107, 107);
    font-size: 14px;
}
.project-card {
    display: flex;
    flex-direction: column;
}

.projects-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.project-card-top {
    display: flex;
    justify-content: space-between;
}

.project-tech-element {
    margin-bottom: 0;
}

/* The container for all links in the top right of the project card */
.project-links {
    display: flex;
    flex-direction: row;
    grid-gap: 20px;
    gap: 20px;
}
.project-link:hover{
    cursor: pointer;
}

.project-card-tech-list {
    padding: 0;
    color: var(--gray);
    display: flex;
    justify-content: left;
    justify-self: end;
    list-style-type: none;
    grid-gap:30px;
    gap:30px;
}


